*{
    box-sizing: border-box;
    margin: 0;
}

/* 
Add variables(if any) here
*/

/* For layout - start*/

.club-title{
    position: absolute;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.club-main-block-image{
    height: 100vh;
}

.club-main-block, .club-block{
    width: 100%;
    display: flex;
    flex-direction: column;
}

.club-block-text{
    height: 70vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px;
}


.club-block-image, .club-main-block-image{
    height: 100vh;
    width: 100%;
}

.club-image{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.club-block-content{
    margin-top: 12px;
}
/*For layout - end*/



/* colors-start */

.club-title{
    color: white;
    font-size: 2.3rem;
}

.club-block{
    background-color: #494848;
    color: #FFFFFF;
}
.club-block:nth-of-type(2n){
    /* This selctor is for providing contrast colors for alternating blocks */
    background-color: #B4B4B4;;
    color: #494848;
}

.club-block-header, .club-block-content{
    inline-size: 350px; 
}

/* colors-end */

@media screen and (min-width:768px) {
    
    .club-title{
        background-color: #B4B4B4;
        width: 40%;
        position: static;
    }

    .club-main-block-image{
        width: 60%;
    }

   .club-main-block, .club-block{
        flex-direction: row;
    }

    .club-block:nth-of-type(2n){
        flex-direction: row-reverse;
    }

    .club-block-image, .club-block-text{
        width: 50%;
        height: 80vh;
    }

    body::-webkit-scrollbar{
        width: 3px;
    }

    body::-webkit-scrollbar-track{
        background-color: #B4B4B4;
    }

    body::-webkit-scrollbar-thumb{
        background-color: #494848;
    }
}

@media screen and (min-width:1200px) {

    .club-title{
        width: 30%;
    }

    .club-main-block-image{
        width: 70%;
    }

    .club-block-text{
        width: 55%;
    }
    .club-block-image{
        width: 45%;
    }
}